import Coreapi from '@/script/common/core-api.js'
import Appapi from '@/script/wx_core_api.js'

import uniSwiperDot from "@/components/uni-swiper-dot/uni-swiper-dot.vue"
import ssNavbar3 from '@/components/ss-navbar3/ss-navbar3.vue'
export default {
	components: {
		uniSwiperDot,
		ssNavbar3
	},
	data() {
		return {
			confid: "1",
			//项目的基础URL
			baseurl: "",
			//轮播图
			info: [],
			confmodel: {},
			current: 0,
			mode: 'round',
			//Nav文字数组
			navArr3str: "",
			navArr3: [{
				title: '介绍',
				content: ""
			}, ],
			currentIndex3: 0,
			//是否签到及签到名词
			hassignin: '-', //-,Y,N
			signordernum: -1,
		}
	},
	onLoad: function(options) {
		var that = this;
		console.log(decodeURIComponent(options.scene));
		if (options.scene != null && options.scene != "") {
			try {
				var scenes = options.scene.split("&");
				var scenejson = {};
				for (var i = 0, j = scenes.length; i < j; i++) {
					var item = scenes[i].split("=");
					scenejson[item[0]] = item[1];
				}
				console.log(scenejson);
				that.confid = scenejson.confid;
				console.log("that.confid=" + that.confid);
			} catch (e) {}
		}

		//获取会议信息		
		Appapi.core_bui_ajax(
			"indexajax/getFullConference", {
				id: that.confid
			},
			function(res) {
				that.baseurl = res.data.data.baseurl;

				that.info = res.data.data.conf.imagearray;
				that.navArr3 = res.data.data.confinfos;
				if (that.navArr3.length > 0) {
					//that.navArr3str = that.navArr3[0].content;
					that.navArr3str = that.navArr3[0].content.replace(new RegExp('src="/', "gm"), 'src="' + that.baseurl + '/');
					console.log(that.navArr3str);
				}
				that.confmodel = res.data.data.conf;
			}



		);

		var that2 = this;
		//获取微信的用户信息
		uni.login({
			provider: 'weixin',
			success: function(loginRes) {
				console.log(loginRes);
				// 获取用户信息
				uni.getUserInfo({
					provider: 'weixin',
					success: function(infoRes) {
						Appapi.core_bui_ajax(
							"weixinajax/loginbyweixincode", {
								code: loginRes.code,
								avatarUrl: infoRes.userInfo.avatarUrl,
								city: infoRes.userInfo.city,
								country: infoRes.userInfo.country,
								gender: infoRes.userInfo.gender,
								language: infoRes.userInfo.language,
								nickName: infoRes.userInfo.nickName,
								province: infoRes.userInfo.province
							},
							function(res) {
								Appapi.core_bui_storage_set("csmlogintoken", res.data.data.openid);
								//获取签到信息
								Appapi.core_bui_ajax(
									"indexajax/getSignininfo", {
										id: that.confid
									},
									function(res) {
										var confuser = res.data.data.confuser;

										if (confuser != null && confuser.signinstatus == 'Y') {
											that2.hassignin = 'Y';
											that2.signordernum = confuser.signordernum;
										} else {
											that2.hassignin = 'N';
										}

									}
								);
							}
						);
					}
				});
			}
		});
	},
	methods: {
		//获取用户的手机号码，需要先获取用户信息
		getPhoneNumber(e) {
			var that = this;
			console.log(e.detail.errMsg);
			console.log(e.detail.iv);
			console.log(e.detail.encryptedData);
			if (e.detail.errMsg == "getPhoneNumber:ok") {
				//提交微信的手机加密信息，加密后提交签到按钮
				Appapi.core_bui_ajax(
					"weixinajax/logintogetmobile", {
						encryptedData: e.detail.encryptedData,
						iv: e.detail.iv
					},
					function(res) {
						Appapi.core_bui_ajax(
							"indexajax/submitsigin", {
								id: that.confid
							},
							function(res) {
								console.log(res);
								that.hassignin = "Y";
								that.signordernum = res.data.data.signordernum;
								Appapi.core_bui_toast("签到成功，您是第" + that.signordernum + "个签到人！");
							}
						);
					}
				);
			} else {
				Appapi.core_bui_toast("获取手机失败，无法签到！");
			}
		},
		change(e) {
			this.current = e.detail.current;
		},
		navbarTapHandler3(index) {
			this.currentIndex3 = index;
			var obj = this.navArr3[index];
			this.navArr3str = obj.content;
		},
		// //获取用户信息
		// clickSigninBtn(e) {

		// }

	}
}
